import React from 'react'
import styles from './image.module.scss'
import IntroducePage from './introduce'

const ImagePage = ({ title, imageSrc }) => {
  return (
    <IntroducePage>
      <span>{title}</span>
      <img className={styles.img} src={imageSrc} alt="" />
    </IntroducePage>
  )
}

export default ImagePage