import React from 'react'
import styles from './camera.module.scss'
import BasicPage from './basic'
import cameraSrc from '../../assets/images/camera.png'
import videoSrc from '../../assets/images/video.png'

const openCamera = () => {
  let input = document.createElement('input');
  input.type = "file"
  input.accept = "image/*"
  input.capture = "camera"
  input.click()
}

const CameraPage = ({ title }) => {
  return (
    <BasicPage>
      <div className={styles.container}>
        <div className={styles.title}><span>{title}</span></div>
        <div className={styles.imageContainer}>
          <div className={styles.leftImage} onClick={openCamera}>
            <img src={cameraSrc} />
            <h4>拍照</h4>
          </div>
          <div className={styles.divider}></div>
          <div className={styles.rightImage} onClick={openCamera}>
            <img src={videoSrc} />
            <h4>錄影</h4>
          </div>
        </div>
      </div>
    </BasicPage>
  )
}

export default CameraPage