import React, { Component } from 'react'
import styles from './header.module.scss'
import Tab from '../common/tab'
import TabItem from '../common/tablItem'
import withMainContext from '../enhancers/withMainContext'

const Header = ({
  mainCtx: {
    activeNumber,
    setActiveNumber
  }
}) => {
  return (
    <div className={styles.container}>
      <Tab activeTabNumber={activeNumber} handleTabClick={setActiveNumber}>
        <TabItem>放置故障標誌</TabItem>
        <TabItem>拍攝全景</TabItem>
        <TabItem>拍攝碰撞點車損部位</TabItem>
        <TabItem>盡速將車移至安全地點</TabItem>
        <TabItem>完成</TabItem>
      </Tab>
    </div>
  )
}

export default withMainContext(Header)