import React, { Fragment } from 'react'
import withMainContext from './enhancers/withMainContext'
import IntroducePage from './content/introduce'
import BasicPage from './content/basic'
import ImagePage from './content/image'
import CameraPage from './content/camera'
import { STEP } from './constants/step'
import putFlagImage from '../assets/images/put-flag-at-the-road.png'
import moveCarImage from '../assets/images/move-car.png'

const PageStart = ({ nextPage }) => (
  <IntroducePage fade={true} onAnimationEnd={nextPage}>
    <span>發生輕微車禍了嗎？</span>
    <span>別擔心，此教學將會給予您正確的指示</span>
  </IntroducePage>
)

const PageHint = (
  <IntroducePage>
    <span>請注意安全</span>
    <span>並依照以下指示操作</span>
  </IntroducePage>
)

const PageOne = (
  <ImagePage 
    imageSrc={putFlagImage}
    title={'請放置故障標誌'}
  />
)

const PageTwo = (
  <CameraPage
    title={'拍攝全景（含標線）'}
  />
)

const PageThree = (
  <CameraPage
    title={'拍攝碰撞點及車損部位'}
  />
)

const PageFour = (
  <ImagePage 
    imageSrc={moveCarImage}
    title={'盡速將車移至安全地點'}
  />
)

const PageEnd = (
  <IntroducePage>
    <span>已完成</span>
    <span>請您保存好檔案</span>
    <span>並提供給相關單位處理</span>
  </IntroducePage>
)

const renderPage = ({ activeNumber: number, nextPage }) => {
  switch(number) {
    case STEP.START:
      return <PageStart nextPage={nextPage} />
    case STEP.HINT:
      return PageHint
    case STEP.ONE:
      return PageOne
    case STEP.TWO:
      return PageTwo
    case STEP.THREE:
      return PageThree
    case STEP.FOUR:
      return PageFour
    case STEP.END:
      return PageEnd
    default:
      return <BasicPage />
  }
}

const Switcher = ({
  mainCtx: {
    activeNumber,
    nextPage,
    prevPage
  }
}) => {
  const arg = { activeNumber, nextPage, prevPage }

  return (
    <Fragment>
      { renderPage(arg) }
    </Fragment>
  )
}

export default withMainContext(Switcher)