import React from 'react'
import BasicPage from './basic'
import styles from './introduce.module.scss'
import Fade from '@material-ui/core/Fade'
import classNames from 'classnames'

const IntroducePage = ({ children, onAnimationEnd, fade }) => {
  const containerProps = {
    className: classNames(styles.container, { [styles.fade]: fade } ),
    ...onAnimationEnd && { onAnimationEnd: onAnimationEnd }
  }

  const Content = () => (
    <div {...containerProps}>
      {
        children.map((child, index) => (
          <div className={styles.child} key={`intro_page-${index}`}>{child}</div>
        ))
      }
    </div>
  )

  const renderContent = () => {
    if(fade) {
      return (
        <Content />
      )
    } else {
      return <Content />
    }
  }

  return (
    <BasicPage>
      { renderContent() }
    </BasicPage>
  )
}

export default IntroducePage