import React from 'react'
import classNames from 'classnames'
import styles from './tabItem.module.scss'
import { Check } from '@material-ui/icons';

const TablItem = ({ isCompleted, isActive, number, children, handleTabClick }) => {
  const classes4Icon = classNames(styles.icon, {
    [styles.icon__inactive]: !isActive,
    [styles.icon__completed]: isCompleted,
    [styles.icon__active]: isActive
  })
  const classes4Title = classNames(styles.title, {
    [styles.title__inactive]: !isActive,
    [styles.title__completed]: isCompleted,
    [styles.title__active]: isActive
  })

  return (
    <div className={styles.container} onClick={() => handleTabClick(number)}>
      <div className={classes4Icon}>
        { isCompleted ? <Check /> : number }
      </div>
      <div className={classes4Title}>
        { children.props.children }
      </div>
    </div>
  )
}

export default TablItem
