import React, { PureComponent } from 'react'
import { STEP } from '../constants/step'

export const Context = React.createContext()

export default class MainProvider extends PureComponent {

  constructor(props) {
    super(props)
    this.state = {
      activeNumber: STEP.START,
      setActiveNumber: this.setActiveNumber,
      prevPage: this.prevPage,
      nextPage: this.nextPage,
      confirmBtnText: '已了解',
      isShowConfirmBtn: false
    }
  }

  componentDidUpdate(prevProps, prevState) {
    const { activeNumber } = this.state
    let btnText = ''
    let isShowBtn = false

    if(activeNumber === STEP.HINT){
      btnText = '已了解'
      isShowBtn = true
    } else if (
      activeNumber === STEP.ONE ||
      activeNumber === STEP.TWO ||
      activeNumber === STEP.THREE ||
      activeNumber === STEP.FOUR
    ) {
      btnText = '已完成'
      isShowBtn = true
    } else {
      isShowBtn = false
    }

    this.setState({
      confirmBtnText: btnText,
      isShowConfirmBtn: isShowBtn
    })
  }
  

  setActiveNumber = (number) => {
    this.setState({
      activeNumber: number
    })
  }

  prevPage = () => {
    this.setState(prevState => ({
      activeNumber: prevState.activeNumber - 1
    }))
  }

  nextPage = () => {
    this.setState(prevState => ({
      activeNumber: prevState.activeNumber + 1
    }))
  }

  render() {
    const { activeNumber } = this.state
    return (
      <Context.Provider
        value={{
          ...this.state
        }}
      >
        {this.props.children}
      </Context.Provider>
    )
  }
}
