import React from 'react'
import styles from './footer.module.scss'
import withMainContext from '../enhancers/withMainContext'

const Footer = ({
  mainCtx: {
    nextPage,
    prevPage,
    confirmBtnText,
    isShowConfirmBtn
  }
}) => {
  return (
    <div className={styles.container}>
      { 
        isShowConfirmBtn && (
          <button 
            className={styles.confirmBtn}
            onClick={nextPage}
          >
            {confirmBtnText}
          </button>
      )}
      <h4>※此教學並不會存放任何您的檔案及資料</h4>
      <h4>新北市政府警察局 - 輕微車禍拍照、錄影五原則</h4>
    </div>
  )
}

export default withMainContext(Footer)